.projects .container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.projects__title {
  font-size: 20px;
  line-height: 36px;
  color: #7B4AE2;
  background-color: rgba(123, 74, 226, 0.1);
  border-radius: 16px;
  margin: 0;
  margin-bottom: 28px;
  padding: 5px 9px 5px 36px;
  position: relative;
}
.projects__title::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 9px;
  width: 20px;
  height: 20px;
  background: url("../img/emojis/chain.png") no-repeat;
}

.projects__subtitle {
  font-size: 48px;
  font-weight: 800;
  line-height: 60px;
  margin: 0;
  margin-bottom: 100px;
}

.projects__list {
  list-style-type: none;
  padding: 0;
  display: grid;
  gap: 55px;
  grid-template-columns: repeat(3, auto);
}

.projects-card {
  border: 1px solid rgba(123, 74, 226, 0.5);
  border-radius: 16px;
  padding: 32px 42px;
}
.projects-card:hover {
  transform: scale(1.04);
  transition: 0.3s ease;
}

.projects-card__title {
  font-size: 20px;
  line-height: 30px;
  margin: 0;
  margin-bottom: 16px;
}

.projects-card__img img {
  display: block;
  margin: 32px 0;
  border-radius: 8px;
}

.projects-card__link {
  width: 50%;
}
.projects-card__link:hover {
  box-shadow: 0 0 5px 1px #7B4AE2;
  transition: 0.3s ease;
}

.tools {
  display: flex;
  list-style-type: none;
  padding: 0;
}

.tools {
  margin: 0;
}

.tools__element {
  color: #7B4AE2;
  margin: 0;
  padding: 5px 14px;
  border-radius: 8px;
  background-color: rgba(123, 74, 226, 0.1);
  text-align: center;
}
.tools__element:not(:last-child) {
  margin-right: 11px;
}
.tools__element--online {
  color: #4AE290;
  background: rgba(74, 226, 144, 0.3);
}
.tools__element--online:hover {
  box-shadow: 0 0 5px 1px #4AE290;
}

